import React, { Component } from 'react';
import { Icon } from 'office-ui-fabric-react/lib/Icon';
export class ShowDocuments extends React.Component {
    
      constructor(props) {
          debugger;
        super(props);

      }

      render() {
        return (
          <tr>
            <td> <Icon iconName='CollapseMenu' className='menuicon' /></td>
            <td>{this.props.myNumber.ID}</td>
            <td>{this.props.myNumber.FileLeafRef}</td>
            <td>{this.props.myNumber.Modified}</td>
          </tr>
    
        );
      }

    }


